import React, { useState } from 'react';
import { Box, Button } from '@chakra-ui/react';
import { Sidebar } from './components/Sidebar';

function App() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);

  const onAddCourse = () => alert('코스 추가 기능 실행');
  const onDeleteCourse = () => alert('코스 삭제 기능 실행');
  const onAddRow = () => alert('인원 추가 기능 실행');
  const onAddDay = () => alert('일차 추가 기능 실행');
  const onDeleteDay = () => alert('일차 삭제 기능 실행');
  const onOpenTournamentList = () => alert('대회 목록 열기');
  const onCaptureGroupSheet = () => alert('스코어 카드 캡쳐 기능 실행');
  const onPrintBusinessCards = () => alert('명찰 출력 기능 실행');
  const onOpenParModal = () => alert('파 설정 열기');
  const onAssignOrder = () => alert('순서 배정 기능 실행');
  const onPrintGroupTable = () => alert('조 편성표 출력 기능 실행');
  const onNewTournament = () => alert('새 대회 생성 기능 실행');
  const onGenerateTestData = () => alert('테스트 데이터 생성 기능 실행');
  const onCaptureTable = () => alert('테이블 캡쳐 기능 실행');
  const onOpenDivisionModal = () => alert('부 설정 열기');
  const onOpenTitleModal = () => alert('추가 제목 열기');
  const onDeleteEmptyRows = () => alert('빈 인원 삭제 기능 실행');

  return (
    <Box display="flex">
      <Sidebar 
        isOpen={isSidebarOpen} 
        onToggle={toggleSidebar}
        onAddCourse={onAddCourse}
        onDeleteCourse={onDeleteCourse}
        onAddRow={onAddRow}
        onAddDay={onAddDay}
        onDeleteDay={onDeleteDay}
        onOpenTournamentList={onOpenTournamentList}
        onCaptureGroupSheet={onCaptureGroupSheet}
        onPrintBusinessCards={onPrintBusinessCards}
        onOpenParModal={onOpenParModal}
        onAssignOrder={onAssignOrder}
        onPrintGroupTable={onPrintGroupTable}
        onNewTournament={onNewTournament}
        onGenerateTestData={onGenerateTestData}
        onCaptureTable={onCaptureTable}
        division="남성부"
        onOpenDivisionModal={onOpenDivisionModal}
        onOpenTitleModal={onOpenTitleModal}
        onDeleteEmptyRows={onDeleteEmptyRows}
      />
      <Box flex="1" ml={isSidebarOpen ? "180px" : "30px"} p={4}>
        <Button onClick={toggleSidebar} mb={4}>Toggle Sidebar</Button>
        <Box textAlign="center">
          <h1>Park Golf Frontend</h1>
          <p>기존 기능이 모두 정상 동작하는 새로운 Chakra UI 프론트엔드 입니다.</p>
          <Button colorScheme="teal" size="lg">기능 실행</Button>
        </Box>
      </Box>
    </Box>
  );
}

export default App; 